import React from 'react'
import Card from '../components/Card'
import Intro from '../components/Intro'



const intro ={
	content:`Premium Online Course merupakan platform belajar coding online dikemas secara interaktif dengan beragam media belajar. Kamu bisa belajar dan mengembangkan skill pemrograman melalui program ini.`,
}
const features = [
	{title : 'Mari Belajar' , desc:'Mari belajar react'},

	{title: 'Waktu Belajar' , desc:'waktu belajar sangat fleksibel'},

	{title:'Metode Belajar' , desc:'kamu hanya perlu mengikuti alur belajar'},


]

const cards = [
	{title:'Menjadi Web Programmer', imageUrl:'https://www.onlc.com/blog/wp-content/uploads/2017/07/ONLC-2017-4-637x350.png'},
	{title:'Belajar Pemrograman PHP', imageUrl:'https://www.codingbytes.com/wp-content/uploads/2019/05/pro.jpg'},

]


const Premium = () => (
	<div className="container">
	<h1 style={{ textAlign: 'center'}}> PREMIUM ONLINE COURSE </h1>
		<div className="row" style={{marginTop:40, marginBottom:100}}>
			<Intro content={intro.content}/>
			<div className="col-md-6" style={styles.box450border}>
				{
					features.map(feature => (
							<div>
								<h5><strong>{feature.title}</strong></h5>
								<p>{feature.desc}</p>
							</div>

						))
				}
			</div>
		</div>
		<div className="row" style={{ marginBottom:50}}>
		{
			cards.map(card =>
				<Card
					imageUrl={card.imageUrl}
					title={card.title}
				/>
				)
		}
		</div>

	</div>
)

const styles = {
	box450border : {
		height: '450px',
		border: '2px solid #f1f1f1',
		borderRadius:10,
		padding: 30
	},
}

export default Premium