import React from 'react'
import Card from '../components/Card'
import Intro from '../components/Intro'

const intro ={
	content:`Program pelatihan pemrograman dengan tatap muka secara offline dan bimbingan para instruktur profesional`,
}

const cards =[
	{
		imageUrl:'https://ect.co.in/wp-content/uploads/2017/02/careers-in-computer-programming.jpg',
		title: 'Weekend Bootcamp',
		subtitle : 'Ayo Bootcamp'

	},
	{
		imageUrl:'https://enjoylife.com.ar/novedades/wp-content/uploads/2018/12/Desarrollador-750x398.jpg',
		title: 'Intensive Bootcamp',
		subtitle : 'Ayo Intensive Bootcamp'

	},

]

const DevSchool = () => (
	<div className="container">
		<h1 style={{ textAlign: 'center'}}> DEVELOPER SCHOOL </h1>
		<div className="row" style={{marginTop:40, marginBottom:50}}>
			<Intro content={intro.content}/>
			<div className="col-md-6">
				<img 
					src="https://lh3.googleusercontent.com/proxy/Qj74Oo-NxtOqS9CFikFmb_td2dRf6ci5o3aCkcooS9fJHBfImtSG8BGJC24cXTank0BhNFz-Qcdyhc7C"
					alt=""
					style={{width:540, heigt:290}}
				/>
			</div>
		</div>
		<div className="row" style={{ marginBottom:50}}>
		{
			cards.map(card =>
				<Card
					imageUrl={card.imageUrl}
					title={card.title}
					subtitle={card.subtitle}
				/>
				)
		}
		</div>

	</div>
)


export default DevSchool