import React from 'react'

const surats = (props) =>(
	props.surats.map((surat) =>(
		<div className="col-md-12" key={surat.nomor}>
          <div className="card" style={{margin :10}}>
              <div className="card-body">
              	  <h2 className="card-title" style={{ textAlign: 'center'}}>{surat.nama} ({surat.asma})</h2>
                  <p><b>Ayat :</b> {surat.ayat}</p>
                  <p><b>Arti :</b> {surat.arti}</p>
                  <p><b>Keterangan :</b> {surat.keterangan}</p>
                  <a href={surat.audio} target="_blank">Audio</a>
              </div>
          </div>
      </div>

		))

)
export default surats