import React,{ Component } from 'react';
import axios from 'axios';
import Surats from './Surats'


class Blog extends Component{
	constructor(props){
		super(props)
		this.state ={
			surats : [],
		}
	}

	componentDidMount(){
    axios.get('https://al-quran-8d642.firebaseio.com/data.json?print=pretty')
      .then(response => {
          this.setState({ surats : response.data })
      })
      .catch(error => {
          console.log('error',error)
      })
  }

  render(){
  	return(
  		<div className="container">
          <h1 style={{textAlign : 'center'}}>Jangan Baca Codingan mulu, Ayo Baca Al Quran</h1>
          <div className="row">
              <div className="col-md-12">
                  <div className="row">
                      <Surats 
                          surats = {this.state.surats}
        
                      />
                  </div>

              </div>
              
          </div>
      </div>

  	)
  }
}

export default Blog
