import React from 'react'

const Home = () => (
	<div style={styles.container}>
		<img
			style={styles.imageCover}
			src="https://res.cloudinary.com/highereducation/image/upload/c_fill,w_1200,h_628,f_auto,fl_lossy,q_auto/v1561662977/BestColleges.com/computer-programming-careers.jpg"
			alt="belajarcoding"
		/>
		<div style={styles.blockText}>
			<h1>Belajar Coding Sekarang</h1>
			<h4>Lebih dari 2000 modul belajar pemrograman dalam berbagai format yang bisa kamu pelajari</h4>
		</div>
	</div>
)

const styles={
	container:{
		position: 'relative',
		textAlign: 'center',
	},
	imageCover: {
		width: '100%'
	},
	blockText:{
		width: '100%',
		position: 'absolute',
		top : '50%',
		left: '50%',
		transform: 'translate(-50%, -50%)',
		color: 'white'
	}
}

export default Home