import React,{ Component } from 'react';
import axios from 'axios';


class Registration extends Component {

	constructor(props){
	    super(props)
	    this.state = {
	      first_name:'',
	      email :'',
	      buttonDisabled : false,
	      nama_tampil: '',
	      email_tampil:'',
	      registered:'',
	    }
	  }

	  inputOnchangeHandler = (event) =>{
      	this.setState({[event.target.name] : event.target.value})
  	  }
	  componentDidMount(){
	    axios.get('https://reqres.in/api/users?page=1')
	      .then(response => {
	          this.setState({ members : response.data.data })
	      })
	      .catch(error => {
	          console.log('error',error)
	      })
	  }

	  onSubmitHandler = (e) => {
      // alert('form di submit')
      e.preventDefault()
      this.setState({ buttonDisabled:true })
      var payload = {
        first_name : this.state.first_name,
        email : this.state.email
      }
      var url = 'https://reqres.in/api/users'

      axios.post(url,payload)
        .then(response => {
            console.log('response', response.data)
            var members = [...this.state.members]
            members.push(response.data)
            this.setState({ 
            	members, 
            	buttonDisabled:false, 
            	first_name:'', 
            	email:'', 
            	nama_tampil:response.data.first_name, 
            	email_tampil:response.data.email,
            	registered : 'Registered'
            })

        })
        .catch(error => {
          console.log(error)
        })
      
  }
  render(){
    return (
	<div className="container">
	<h1 style={{ textAlign: 'center'}}> REGISTRATION FORM </h1>
		<div className="row" style={{marginTop:40}}>
			
			<div className="col-md-6" style={styles.box450border}>
				<form onSubmit={this.onSubmitHandler}>
		          <div className="form-group">
		              <label>Nama</label>
		              <input type="text" className="form-control" name="first_name" value={this.state.first_name} onChange={this.inputOnchangeHandler}/>
		          </div>
		          <div className="form-group">
		              <label>Email</label>
		              <input type="email" className="form-control" name="email" value={this.state.email} onChange={this.inputOnchangeHandler}/>
		          </div>
		          <button type="submit" className="btn btn-primary" disabled={this.buttonDisabled}>Submit</button>
		      </form>
		     </div>
		      <div className="col-md-6" style={styles.box450border}>
		      <form>
		          <div className="form-group-row">
		          	
		              <label className="col-md-2 col-form-label">Nama</label>
		             <div className="col-md-10">
		              <input type="text" className="form-control" name="nama_tampil" value={this.state.nama_tampil} onChange={this.inputOnchangeHandler} disabled/>
		          	</div>
		          </div>
		          <div className="form-group-row">
		              <label className="col-md-2 col-form-label">Email</label>
		              <div className="col-md-10">
		              <input type="email" className="form-control" name="email_tampil" value={this.state.email_tampil} onChange={this.inputOnchangeHandler} disabled/>
		          	  </div>
		          </div>
		          <div className="form-group-row">
		          	<h4 style={{color:"#006400", textAlign:"center"}}>{this.state.registered}</h4>
		          </div>
		           </form> 
		        </div> 

			
		</div>
	</div>
	);
}
}

const styles = {
	box450border : {
		height: '450px',
		border: '2px solid #f1f1f1',
		borderRadius:10,
		padding: 30
	},
}

export default Registration

